@font-face { font-family: "cargan"; src: url("https://fschoenf.uber.space/rundgang/css/font/carganlig-webfont.woff2") format("woff2"); font-style: normal; font-weight: normal; }

@font-face { font-family: "cargan"; src: url("https://fschoenf.uber.space/rundgang/css/font/carganbol-webfont.woff2") format("woff2"); font-style: normal; font-weight: bold; }

/* @font-face { font-family: "Merriweather"; src: url("/css/font/Merriweather-Regular.ttf") format("ttf"); font-style: normal; font-weight: 400; } @font-face { font-family: "Merriweather"; src: url("/css/font/Merriweather-Italic.ttf") format("ttf"); font-style: italic; font-weight: normal; } @font-face { font-family: "Merriweather"; src: url("/css/font/Merriweather-Bold.ttf") format("ttf"); font-style: normal; font-weight: bold; } */
* { font-family: "Merriweather", serif; }

h1, h2, h3, h4 { font-family: "cargan", serif; font-weight: bold; }

p { line-height: 1.7rem; }

@media (min-width: 767px) { p { margin-left: 5rem; } }

.container { margin: 0 1rem; }

@media (min-width: 767px) { .container { max-width: 60rem; margin: 0 auto; padding: 0 1rem; } }

@media (min-width: 767px) { p { margin-bottom: 1rem; }
  h1 { margin-bottom: 2rem; }
  h2 { margin-bottom: 1rem; } }

.alert { border: 1px solid grey; padding: 1rem; }

.alert.alert-warning { background: #faffb8; }

.button { display: inline-block; padding: 0.75rem 1rem; margin: 0.5rem 0; transition: 0.25s ease opacity; }

.button:hover { opacity: 0.8; }

.button.button-primary { background: #17161b; color: white; }

nav.navbar { margin-bottom: 0.5rem; }

nav.navbar > ul.navbar-nav { display: grid; grid-template: auto auto/repeat(3, 1fr); align-items: center; justify-items: center; }

@media (min-width: 767px) { nav.navbar > ul.navbar-nav { width: 100%; display: flex; justify-content: space-between; align-items: center; } }

nav.navbar > ul.navbar-nav a.navbar-brand { margin: 1rem 0; grid-column: span 3; }

@media (min-width: 767px) { nav.navbar > ul.navbar-nav a.navbar-brand { margin: 1rem; margin-right: 3rem; } }

nav.navbar > ul.navbar-nav a.navbar-brand img.navbar-logo { height: 5rem; }

nav.navbar > ul.navbar-nav > li.nav-item { grid-row: 2; }

@media (min-width: 767px) { nav.navbar > ul.navbar-nav > li.nav-item { padding: 1rem; } }

nav.navbar > ul.navbar-nav > li.nav-item a { text-decoration: none; color: #17161b; }

nav.navbar > ul.navbar-nav > li.nav-item.active { background: #17161b; color: white; font-weight: bold; }

nav.navbar > ul.navbar-nav > li.nav-item.active a { color: white; }

nav.navbar.navbar-dark { /* background: $color-dark-grey; a { color: white; } */ }

:root { --swiper-theme-color: white !important; }

.swiper-container { width: 100%; height: 30rem; margin-bottom: 2rem; }

.swiper-container .swiper-slide--custom { background-position: center !important; background-size: cover !important; background-repeat: no-repeat !important; }

footer { color: #808080; text-align: center; border-top: 1px dashed gray; margin-top: 1rem !important; }

footer > div { margin: 1rem 0; }

.direct-link { display: none; font-size: 1rem; opacity: 0.5; text-decoration: none; }

.direct-link:hover { opacity: 1; }

div.widget--latest-posts { padding: 1rem; background: #17161b; }

div.widget--latest-posts * { color: white; }

div.widget--latest-posts li.postlist-item { margin-bottom: 0.5rem; }

div.widget--latest-posts p.archive-text { color: #b9b9b9; }
